//   
//   , (array),       
//         path     
const sweetConfirmReload = (msg_title, msg_content_array, path) => {
  return sweet
    .fire({
      customClass: {
        popup: "border",
        confirmButton: "confirm",
      },
      width: "auto",
      padding: "0 1rem 1rem",
      title: (
        <Grid>
          <Text size={fontSize.base} bold2="700" margin="0 auto 1rem">
            {msg_title}
          </Text>
          {msg_content_array?.map((p, idx) => {
            let word_first = p.includes(":") ? p.split(":") : null;
            let word_mid = p.includes("/") ? p.split("/") : null;
            let word_normal = !p.includes(":") && !p.includes("/") ? p : null;
            if (word_first) {
              return (
                <Text size={fontSize.small} word_break="keep-all" key={idx}>
                  <b>{word_first[0]}</b>{word_first[1]}
                </Text>
              );
            }
            if (word_mid) {
              return (
                <Text size={fontSize.small} word_break="keep-all" key={idx}>
                  {word_mid[0]} <b>{word_mid[1]}</b>{word_mid[2]}
                </Text>
              );
            } else {
              return (
                <Text size={fontSize.small} word_break="keep-all" key={idx}>
                  {word_normal}
                </Text>
              );
            }
          })}
        </Grid>
      ),
      confirmButtonColor: color.brand100,
      confirmButtonText: (
        <Grid width="15rem" is_flex2 margin="auto">
          <Text padding="0" color={color.bg0} bold2={fontSize.bold}>
            
          </Text>
        </Grid>
      ),
      focusConfirm: false,
    })
    .then((res) => {
      if (res.isConfirmed) {
        //    
        if (path === "") {
          return;
        }
        //   history 
        if (path === "history") {
          history.replace("/home");
          return;
        }
        // 
        if (path === "goBack") {
          history.goBack();
          return;
        }
        //  
        if (path === "/profile") {
          return history.push(path);
        }
        if (path === "/break") {
          return history.push("/chatlist");
        }
        //     
        window.location.replace(path);
      } else if (res.isDismissed) {
        // 
        if (path === "goBack") {
          history.goBack();
          return;
        }
        // 
        if (path === "/mypage") {
          history.push("/mypage");
          return;
        }
        if (path === "/mypost") {
          window.location.replace(path);
          return;
        }
      } else {
        if (path === "/profile") {
          return history.push(path);
        }
        if (path === "/break") {
          return history.push("/chatlist");
        }
        return;
      }
    });
};

