// ,  
const sweetUserInfo = (age, gender) => {
  let age_label = null;
  if (age === "10~19") {
    age_label = "10";
  }
  if (age === "20~29") {
    age_label = "20";
  }
  if (age === "30~39") {
    age_label = "30";
  }
  if (age === "40~49") {
    age_label = "40";
  }
  if (age === "50~59") {
    age_label = "50";
  }

  let gender_label = null;
  if (gender === "female") {
    gender_label = "";
  }
  if (gender === "male") {
    gender_label = "";
  }

  return sweet
    .fire({
      customClass: {
        popup: "border",
        confirmButton: "confirmButton",
        cancelButton: "cancelButton",
        denyButton: "denyButton",
        actions: "meal-action-class",
      },
      width: "auto",
      padding: "0 0 1rem",
      title: (
        <Grid>
          <Text margin="0 auto 1rem" size={fontSize.base} bold2="700">
             /  ?
          </Text>
          <Text size={fontSize.small}>
            /   ,   <br />
              .
          </Text>
          <Grid margin="1.6rem auto">
            <Text size={fontSize.base}>: {gender_label}</Text>
            <Text size={fontSize.base}>: {age_label}</Text>
          </Grid>
        </Grid>
      ),
      showCancelButton: true,
      cancelButtonColor: color.brand20,
      cancelButtonText: (
        <Grid width="9rem" is_flex2 margin="auto">
          <Text padding="0" color={color.brand100} bold2={fontSize.bold}>
             
          </Text>
        </Grid>
      ),
      confirmButtonColor: color.brand100,
      confirmButtonText: (
        <Grid width="9rem" is_flex2 margin="auto">
          <Text padding="0" color={color.bg0} bold2={fontSize.bold}>
            
          </Text>
        </Grid>
      ),
      focusConfirm: false,
      reverseButtons: true,
    })
    .then((res) => {
      if (res.isConfirmed) {
        return true;
      } else {
        return false;
      }
    });
};

const SweetAllowChat = (join_id) => {
  sweet
    .fire({
      customClass: {
        popup: "border",
        confirmButton: "confirmButton",
        cancelButton: "cancelButton",
        denyButton: "denyButton",
        actions: "meal-action-class",
      },
      width: "auto",
      padding: "0 1rem 1rem",
      title: (
        <Grid>
          <Text margin="0 auto 1rem" size={fontSize.base} bold2="700">
            ?
          </Text>
        </Grid>
      ),
      text: "   .",
      showDenyButton: true,
      denyButtonText: (
        <Grid width="9rem" is_flex2 margin="auto">
          <Text padding="0" color={color.brand100} bold2={fontSize.bold}>
            
          </Text>
        </Grid>
      ),
      denyButtonColor: color.brand20,
      confirmButtonColor: color.brand100,
      confirmButtonText: (
        <Grid width="9rem" is_flex2 margin="auto">
          <Text padding="0" color={color.bg0} bold2={fontSize.bold}>
            
          </Text>
        </Grid>
      ),
      focusConfirm: false,
      reverseButtons: true,
    })
    .then((res) => {
      if (res.isConfirmed) {
        axiosModule
          .get(`/posts/join/request/accept/${join_id}?accept=true`)
          .then((res) => {
            logger("  res", res);
            sweetConfirmReload(
              " ",
              ["    ."],
              "/allowchat"
            );
          })
          .catch((e) => {
            logger("    ", e);
            sweetConfirmReload(
              " ",
              ["  .", "  ."],
              ""
            );
          });
      } else if (res.isDenied) {
        return;
      } else {
        return;
      }
    });
};

